  /*-------------------------*/
/* $Typography 
---------------------------*/
/* Globel */
body,html{
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
  }
  /* heading */
  h1,h2,h3,h4,h5,h6{
    margin: 0;
    color: #484848;
  }
  h1{
    font-size: 80px;
    font-weight: 800;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    margin-top: 24px;
    margin-bottom: 24px;
  }
  h2{
    font-size: 48px;
    font-weight: 600;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    margin-top: 24px;
    margin-bottom: 24px;
  }
  h3{
    font-size: 28px;
    font-weight: 400;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    line-height: 48px;
    letter-spacing: 2px;
  }
  h4{
    font-size: 24px;
    font-weight: 500;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    line-height: 32px;
  }
  h5{
    font-size: 20px;
    font-weight: 400;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    line-height: 1;
    margin-top: 10px;
  }
  h6{
    font-size: 16px;
    font-weight: 400;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    line-height: 32px;
    margin-bottom: 16px;
  }
  p{
    font-size: 16px;
    font-weight: 400;
    font-family: 'Helvetica', 'roboto', arial, sans-serif;
    line-height: 16px;
    letter-spacing: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    padding-top: 2px;
  }
  @media(max-width:768px){
    h4{
      font-size: 20px;
      line-height: 28px;
    }
  }
  @media(max-width:576px){
    h3{
      font-size: 20px;
      line-height: 24px;
    }
    h4{
      font-size: 18px;
      line-height: 24px;
    }
    h5{
      font-size: 18px;
    }
    h6{
      font-size: 14px;
    }
  }
  @media(max-width:425px){
    h3{
      font-size: 16px;
      line-height: 20px;
    }
    h4{
      font-size: 16px;
      line-height: 20px;
    }
    h5{
      font-size: 12px;
    }
    h6{
      font-size: 12px;
    }
    p{
      font-size: 12px;
    }
  }
  .subtitle{
    font-size: 48px;
    font-weight: 900;
    font-family: 'Raleway','Helvetica', 'roboto', arial, sans-serif;
    margin-top: 24px;
    margin-bottom: 24px;
  }
  @media (max-width: 768px){
    .subtitle{
      font-size: 40px;
    }
  }
  /* link */
  a{
    color: #484848;
    text-decoration: none;
  }
  a:hover,
  a:active,
  a:fucus{
    color: #484848;
    text-decoration: none;
  }
  /* list */
  ul{
    list-style: none;
    margin: 0;
    padding: 0;
  }

  /* image */
.imgbox .image_inner{
    width: 100%;
    padding-top: 100%;
    position: relative;
    height: 0;
}
.imgbox .image_inner .img{
    border-radius: 50%;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.star{
  float: left;
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.star_img{
  width: 15px;
  height: 15px;
  background-size: cover;
  opacity: 0.9;
  margin-right: 5px;
}
.watcher{
  float: left;
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.watcher_img{
  width: 15px;
  height: 15px;
  background-size: cover;
  opacity: 0.9;
  margin-right: 5px;
}
.requestmore{
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  background-size: cover;
  background-repeat: no-repeat;
}
.start{
  text-align: center;
  margin-top: 140px;
}
.start .title{
  position: absolute;
  top: 400%;
  left: 50%;
  transform: translateX(-50%);
}
.start .imgbox{
  width: 30%;
}
.start .imgbox .image_inner .img{
  border-radius: 0%;
}